@use "global" as *;

.gallery__item {
  cursor: pointer;
  @include mq("md") {
    aspect-ratio: auto;
  }
}

.gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gallery__item0 {
  aspect-ratio: 524 / 345;
  @include mq("md") {
    grid-column: 1 / span 7;
    grid-row: 1 / span 2;
  }
}

.gallery__item1 {
  aspect-ratio: 345 / 219;
  @include mq("md") {
    grid-column: 8 / span 13;
    grid-row: 1;
  }
}

.gallery__item2 {
  aspect-ratio: 345 / 219;
  @include mq("md") {
    grid-column: 8 / span 13;
    grid-row: 2;
  }
}

.gallery__item3 {
  aspect-ratio: 345 / 219;
  @include mq("md") {
    grid-column: 1 / span 9;
    grid-row: 3;
  }
}

.gallery__item4 {
  aspect-ratio: 345 / 219;
  @include mq("md") {
    grid-column: 1 / span 9;
    grid-row: 4;
  }
}

.gallery__item5 {
  aspect-ratio: 524 / 345;
  @include mq("md") {
    grid-column: 10 / span 13;
    grid-row: 3 / span 4;
  }
}
